import React from 'react';
import {
    Button,
    Container

} from 'semantic-ui-react'; 
import API from '../api'
import BlogCard from '../components/BlogCard'
var parseString = require('xml2js').parseString;


class Blog extends React.Component{ 

    state = {}
    
    componentDidMount(){

       let json =  API.getMediumRSS().then(textXml => {
            parseString(textXml, (err, xml) => {

            this.setState({json: xml.rss.channel[0]})
            })
        })
       
    }

    renderBlogCards = () => {
       let blogCards = json.item.map(item => {
            return( <BlogCard blog={item} />)
        })
        this.setState({blogCards: blogCards})
    }

    render(){
        console.log(this.state)
        return(
            <div>
                <Container>
                    {
                        this.state.blogCards
                    }
                </Container>
            </div>
        )
    }
}

export default Blog



